<template>
  <div id="app">
    <b-container>
      <b-form-input v-model="project_id" placeholder="Project ID"></b-form-input>
      <b-form-input v-model="job_id" placeholder="Job ID"></b-form-input>
      <b-form-input v-model="save_path" placeholder="Save Path"></b-form-input>
      <b-form-input v-model="current_file" placeholder="Current File"></b-form-input>
      <b-button variant="success" @click="getTxtJsons()">Get Texts</b-button>
      <b-button variant="success" @click="setTxtJsons()">Set Texts</b-button>

      <b-form-input v-model="port" placeholder="Premiere Port"></b-form-input>
      <b-button variant="success" @click="startPremiere()">Start Premiere Instance</b-button>


      <template v-if="processed_file !== null">
        <b-button variant="success" @click="downloadEdited()">Download Translated File</b-button>
      </template>
      <template v-else-if="row_data.length > 0">
        <b-col cols="12" v-for="(row, index) in row_data" :key="index" class="m-1">
          <b-input type="text" v-model="row_data[index].mogrtJson.textEditValue" />
        </b-col>
        <b-col cols="12">
          <b-button variant="success" @click="sendEdited()">Submit</b-button>
        </b-col>
        <b-col cols="12">
          {{ send_error }}
        </b-col>
      </template>
      <template v-else>
        <b-form-file
          v-model="file"
          :state="Boolean(file)"
          placeholder="Choose a file or drop it here..."
          drop-placeholder="Drop file here..."
        ></b-form-file>
        <b-button variant="success" @click="uploadFile()">Analyze</b-button>
        <b-col cols="12">
          {{ upload_error }}
        </b-col>
      </template>
    </b-container>
  </div>
</template>

